export default {
	props: {
		statusColor: {
			type: String,
			default: '#FFFFFF'
		}
	},
	data() {
		return {
			tagIndex: 0, // 顶部标签
			lpwCode: false,
			rpwCode: false,
			phone: null,
			password: null,
			// 注册数据
			regData: {
				'phone': null,	// 手机号
				'code': null,	// 验证码
				'nickName': null,	// 昵称
				'password': null,	// 密码
			},
			regCheck: false	// 协议
		}
	},
	onLoad() {},
	methods: {
		// 数据提交
		submit: function() {
			let that = this;
			if (this.tagIndex === 0) {
				var phone, password;
				var pattern = /^1[34578]\d{9}$/;
				if (pattern.test(this.phone)) { 
					phone = this.phone
				} else {
					return this.toastMsg('请输入有效手机号')
				}
				// 判断 字符串中是否存在' ', -1表示不存在(this.password.indexOf(' ') != -1)
				var regex = /[a-zA-Z0-9]{6,20}/;
				if (!regex.test(this.password)) {
					return this.toastMsg('请输入至少六位字符密码')
				} else if (this.password.indexOf(' ') != -1) {
					return this.toastMsg('密码不能包含空格')
				} else {
					password = this.password
				}
				let url = '/login/login'
				let data = {
					"password": password,
					"phone": phone
				}
				this.$axios.request({
					url: url,
					method: 'post',
					data: data
				}).then(function(res) {
					if (res.statusCode === 200) {
						if (res.data.resultCode === 1) {
							var token = res.data.data.token,
								userId = res.data.data.userId;
							uni.setStorage({
								key: 'Login_status',
								data: {
									userId: userId,
									token: token
								},
								success: function () {
									console.log(JSON.stringify(uni.getStorageSync('Login_status')));
									uni.showToast({
										icon: 'success',
										mask: true,
										title: '登录成功!'
									})
									uni.switchTab({
										url: '/pages/index/index'
									})
								}
							});
						} else {
							return that.toastMsg('账号密码不匹配!')
						}
					} else {
						return that.toastMsg('发生错误,请稍后再试！')
					}
				}).catch(function(error) {
					// 处理 getJSON 和 前一个回调函数运行时发生的错误
					console.log('发生错误！', error)
				})
			}
			else if (this.tagIndex === 1) {
				var phone, code, nickName, password;
				phone = that.regData.phone;
				code = that.regData.code;
				nickName = that.regData.nickName.replace(/(^\s*)|(\s*$)/g, ""); // 去掉首尾空格
				password = that.regData.password;
				var checkCode = that.checking(phone, code, nickName, password)
				if (checkCode) {
					if (!that.regCheck) {
						return that.toastMsg('请阅读用户协议!')
					}
					// console.log(phone, code, nickName, password);
					let data = {
						"phone": phone,
						"code": code,
						"nickName": nickName,
						"password": password
					}
					this.$axios.request({
						url: '/login/regist',
						method: 'post',
						data: data
					}).then(function(res) {
						if (res.statusCode === 200) {
							if (res.data.resultCode === 1) {
								that.tagIndex = 0;
								that.regData.phone = '';
								that.regData.code = '';
								that.regData.nickName = '';
								that.regData.password = '';
								uni.showToast({
									icon: 'success',
									title: '注册成功!'
								})
							} else if (res.data.resultCode === 102) {
								// 验证码失效
								return that.toastMsg(res.data.resultMsg)
							}
						} else {
							return that.toastMsg('发生错误,请稍后再试！')
						}
					}).catch(function(error) {
						// 处理 getJSON 和 前一个回调函数运行时发生的错误
						console.log('发生错误！', error)
					})
				}
				// else {
				// 	console.log('fail')
				// }
			}
		},
		// 发送验证码
		sendSms: function () {
			let that = this;
			var pattern = /^1[34578]\d{9}$/;
			if (that.regData.phone == '' || !pattern.test(that.regData.phone)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确手机号',
				})
				return false;
			}
			this.$axios.request({
				url: '/login/sendSms',
				method: 'post',
				data: {
					"phone": that.regData.phone
				}
			}).then(function(res) {
				// console.log(typeof(res.statusCode),typeof(res.data.resultCode)); // number number
				if (res.statusCode === 200 && res.data.resultCode === 1) {
					uni.showToast({
						icon: 'none',
						title: '发送成功，请注意查收!',
					})
				}
			})
		},
		// 格式验证
		checking: function (a,b,c,d) {
			// 手机号
			var regA = /^1[34578]\d{9}$/;
			if (!regA.test(a)) {
				this.toastMsg('请输入有效手机号')
				return 0;
			}
			// 验证码
			var regB = new RegExp(/^\d{6}$/);
			if (!regB.test(b)) {
				this.toastMsg('请输入正确验证码')
				return 0;
			}
			// 昵称(首尾不能为空，中间可输入空格)
			var regC = new RegExp(/^[\w\u4e00-\u9fa5\-_][\s\w\u4e00-\u9fa5\-_]*[\w\u4e00-\u9fa5\-_]$/);
			if (!regC.test(c)) {
				this.toastMsg('请输入有效昵称')
				return 0;
			}
			// 密码
			var regD = new RegExp(/[a-zA-Z0-9]{6,20}/);
			if (!regD.test(d)) {
				this.toastMsg('请输入至少六位字符密码')
				return 0;
			} else if (d.indexOf(' ') != -1) {
				this.toastMsg('密码不能包含空格')
				return 0;
			}
			// 验证通过
			return 1;
		},
		// 详情页
		openDetail: function(e) {
			uni.navigateTo({
				url: '/pages/detail/detail'
			})
		},
		checkboxChange: function(e) {
			this.regCheck = !this.regCheck;
		},
		// 提示内容
		toastMsg: function (e) {
			uni.showToast({
				icon: 'none',
				title: e,
				position: 'bottom' // 5+app
			})
			return false;
		},
		// 返回上一页
		backButton: function() {
			// uni.navigateBack({ delta: 1 })
			uni.navigateBack();
		}
	}
}
